import { createSelector } from '@ngrx/store';

import { ProfileStateInterface } from './profileState.interface';
import { AppStateInterface } from '@store/app/app-state.interface';

export const profileFeatureSelector = (state: AppStateInterface): ProfileStateInterface => state.profile;
export const selectIsLoadingProfile = createSelector(profileFeatureSelector, (state: ProfileStateInterface) => state.isLoading);
export const selectIsLoadingProfileFeed = createSelector(profileFeatureSelector, (state: ProfileStateInterface) => state.isLoadingFeed);
export const selectUserLoginInfo = createSelector(profileFeatureSelector, (state: ProfileStateInterface) => state.loginInfo);
export const selectUserPosts = createSelector(profileFeatureSelector, (state: ProfileStateInterface) => state.posts);
